// SCSS variables are information about icon's compiled state, stored under its original file name
//
// .icon-home {
//   width: $icon-home-width;
// }
//
// The large array-like variables contain all information about a single icon
// $icon-home: x y offset_x offset_y width height total_width total_height image_path;
//
// At the bottom of this section, we provide information about the spritesheet itself
// $spritesheet: width height image $spritesheet-sprites;
$about-name: 'about';
$about-x: 0px;
$about-y: 0px;
$about-offset-x: 0px;
$about-offset-y: 0px;
$about-width: 47px;
$about-height: 47px;
$about-total-width: 99px;
$about-total-height: 99px;
$about-image: '../images/sprite.png';
$about: (0px, 0px, 0px, 0px, 47px, 47px, 99px, 99px, '../images/sprite.png', 'about', );
$experience-name: 'experience';
$experience-x: 52px;
$experience-y: 0px;
$experience-offset-x: -52px;
$experience-offset-y: 0px;
$experience-width: 47px;
$experience-height: 47px;
$experience-total-width: 99px;
$experience-total-height: 99px;
$experience-image: '../images/sprite.png';
$experience: (52px, 0px, -52px, 0px, 47px, 47px, 99px, 99px, '../images/sprite.png', 'experience', );
$personal-name: 'personal';
$personal-x: 0px;
$personal-y: 52px;
$personal-offset-x: 0px;
$personal-offset-y: -52px;
$personal-width: 47px;
$personal-height: 47px;
$personal-total-width: 99px;
$personal-total-height: 99px;
$personal-image: '../images/sprite.png';
$personal: (0px, 52px, 0px, -52px, 47px, 47px, 99px, 99px, '../images/sprite.png', 'personal', );
$about-2x-name: 'about@2x';
$about-2x-x: 0px;
$about-2x-y: 0px;
$about-2x-offset-x: 0px;
$about-2x-offset-y: 0px;
$about-2x-width: 94px;
$about-2x-height: 94px;
$about-2x-total-width: 198px;
$about-2x-total-height: 198px;
$about-2x-image: '../images/sprite@2x.png';
$about-2x: (0px, 0px, 0px, 0px, 94px, 94px, 198px, 198px, '../images/sprite@2x.png', 'about@2x', );
$experience-2x-name: 'experience@2x';
$experience-2x-x: 104px;
$experience-2x-y: 0px;
$experience-2x-offset-x: -104px;
$experience-2x-offset-y: 0px;
$experience-2x-width: 94px;
$experience-2x-height: 94px;
$experience-2x-total-width: 198px;
$experience-2x-total-height: 198px;
$experience-2x-image: '../images/sprite@2x.png';
$experience-2x: (104px, 0px, -104px, 0px, 94px, 94px, 198px, 198px, '../images/sprite@2x.png', 'experience@2x', );
$personal-2x-name: 'personal@2x';
$personal-2x-x: 0px;
$personal-2x-y: 104px;
$personal-2x-offset-x: 0px;
$personal-2x-offset-y: -104px;
$personal-2x-width: 94px;
$personal-2x-height: 94px;
$personal-2x-total-width: 198px;
$personal-2x-total-height: 198px;
$personal-2x-image: '../images/sprite@2x.png';
$personal-2x: (0px, 104px, 0px, -104px, 94px, 94px, 198px, 198px, '../images/sprite@2x.png', 'personal@2x', );
$spritesheet-width: 99px;
$spritesheet-height: 99px;
$spritesheet-image: '../images/sprite.png';
$spritesheet-sprites: ($about, $experience, $personal, );
$spritesheet: (99px, 99px, '../images/sprite.png', $spritesheet-sprites, );
$retina-spritesheet-width: 198px;
$retina-spritesheet-height: 198px;
$retina-spritesheet-image: '../images/sprite@2x.png';
$retina-spritesheet-sprites: ($about-2x, $experience-2x, $personal-2x, );
$retina-spritesheet: (198px, 198px, '../images/sprite@2x.png', $retina-spritesheet-sprites, );

// These "retina group" variables are mappings for the naming and pairing of normal and retina sprites.
//
// The list formatted variables are intended for mixins like `retina-sprite` and `retina-sprites`.
$about-group-name: 'about';
$about-group: ('about', $about, $about-2x, );
$experience-group-name: 'experience';
$experience-group: ('experience', $experience, $experience-2x, );
$personal-group-name: 'personal';
$personal-group: ('personal', $personal, $personal-2x, );
$retina-groups: ($about-group, $experience-group, $personal-group, );

// The provided mixins are intended to be used with the array-like variables
//
// .icon-home {
//   @include sprite-width($icon-home);
// }
//
// .icon-email {
//   @include sprite($icon-email);
// }
//
// Example usage in HTML:
//
// `display: block` sprite:
// <div class="icon-home"></div>
//
// To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:
//
// // CSS
// .icon {
//   display: inline-block;
// }
//
// // HTML
// <i class="icon icon-home"></i>
@mixin sprite-width($sprite) {
  width: nth($sprite, 5);
}

@mixin sprite-height($sprite) {
  height: nth($sprite, 6);
}

@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 3);
  $sprite-offset-y: nth($sprite, 4);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite-image($sprite) {
  $sprite-image: nth($sprite, 9);
  background-image: url(#{$sprite-image});
}

@mixin sprite($sprite) {
  @include sprite-image($sprite);
  @include sprite-position($sprite);
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}

// The `retina-sprite` mixin sets up rules and a media query for a sprite/retina sprite.
//   It should be used with a "retina group" variable.
//
// The media query is from CSS Tricks: https://css-tricks.com/snippets/css/retina-display-media-query/
//
// $icon-home-group: ('icon-home', $icon-home, $icon-home-2x, );
//
// .icon-home {
//   @include retina-sprite($icon-home-group);
// }
@mixin sprite-background-size($sprite) {
  $sprite-total-width: nth($sprite, 7);
  $sprite-total-height: nth($sprite, 8);
  background-size: $sprite-total-width $sprite-total-height;
}

@mixin retina-sprite($retina-group) {
  $normal-sprite: nth($retina-group, 2);
  $retina-sprite: nth($retina-group, 3);
  @include sprite($normal-sprite);

  @media (-webkit-min-device-pixel-ratio: 2),
         (min-resolution: 192dpi) {
    @include sprite-image($retina-sprite);
    @include sprite-background-size($normal-sprite);
  }
}

// The `sprites` mixin generates identical output to the CSS template
//   but can be overridden inside of SCSS
//
// @include sprites($spritesheet-sprites);
@mixin sprites($sprites) {
  @each $sprite in $sprites {
    $sprite-name: nth($sprite, 10);
    .#{$sprite-name} {
      @include sprite($sprite);
    }
  }
}

// The `retina-sprites` mixin generates a CSS rule and media query for retina groups
//   This yields the same output as CSS retina template but can be overridden in SCSS
//
// @include retina-sprites($retina-groups);
@mixin retina-sprites($retina-groups) {
  @each $retina-group in $retina-groups {
    $sprite-name: nth($retina-group, 1);
    .#{$sprite-name} {
      @include retina-sprite($retina-group);
    }
  }
}
