}

.text-body {
  color: #212529 !important;
}

.text-muted {
  color: #6c757d !important;
}

.text-black-50 {
  color: rgba(0, 0, 0, 0.5) !important;
}

.text-white-50 {
  color: rgba(255, 255, 255, 0.5) !important;
}

.text-hide {
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}

.visible {
  visibility: visible !important;
}

.invisible {
  visibility: hidden !important;
}

/*==========================================
                LAYOUT
==========================================*/
body {
  position: relative;
  display: flex;
  min-height: 100vh;
  margin: 0;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #000000;
}

.wrapper {
  position: relative;
  max-width: 1200px;
  height: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.wrapper:after {
  content: "";
  display: table;
  clear: both;
}

.main {
  padding-bottom: 70px;
}

.section {
  margin-top: 35px;
}

@media screen and (min-width: 768px) {
  .section {
    margin-top: 70px;
  }
}

.section-title {
  text-align: center;
  margin-bottom: 25px;
}

@media screen and (min-width: 768px) {
  .section-title {
    margin-bottom: 50px;
  }
}

.section-title__icon {
  margin-bottom: 15px;
}

@media screen and (min-width: 768px) {
  .section-title__icon {
    margin-bottom: 25px;
  }
}

.section-title__paragraph {
  margin-top: 15px;
  max-width: 520px;
  margin-left: auto;
  margin-right: auto;
}

.headling {
  display: block;
  font-size: 20px;
  line-height: 1;
  font-weight: 300;
  text-align: center;
}

@media screen and (min-width: 768px) {
  .headling {
    font-size: 30px;
  }
}

.headling_large {
  font-weight: 100;
  font-size: 50px;
}

@media screen and (min-width: 768px) {
  .headling_large {
    font-size: 70px;
  }
}

.icon {
  display: inline-block;
  vertical-align: middle;
}

.icon_personal {
  background-image: url(../images/sprite.png);
  background-position: 0px -52px;
  width: 47px;
  height: 47px;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .icon_personal {
    background-image: url(../images/sprite@2x.png);
    background-size: 99px 99px;
  }
}

.icon_experience {
  background-image: url(../images/sprite.png);
  background-position: -52px 0px;
  width: 47px;
  height: 47px;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .icon_experience {
    background-image: url(../images/sprite@2x.png);
    background-size: 99px 99px;
  }
}

.icon_about {
  background-image: url(../images/sprite.png);
  background-position: 0px 0px;
  width: 47px;
  height: 47px;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .icon_about {
    background-image: url(../images/sprite@2x.png);
    background-size: 99px 99px;
  }
}

/*==========================================
                    FORM
==========================================*/
.form {
  padding: 0 15px;
}

.form__group {
  max-width: 520px;
  margin-left: auto;
  margin-right: auto;
}

.form__row {
  margin-bottom: 25px;
}

.form__row:last-child {
  margin-bottom: 0;
}

@media screen and (min-width: 420px) {
  .form__row_small {
    max-width: 320px;
    margin-left: auto;
    margin-right: auto;
  }
}

.form__column {
  flex: 0 0 100%;
  max-width: 100%;
  margin-bottom: 15px;
}

@media screen and (min-width: 420px) {
  .form__column {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

@media screen and (min-width: 768px) {
  .form__column {
    flex: 0 0 33.33333%;
    max-width: 33.33333%;
  }
}

.form__checkboxed {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 700px;
  margin-bottom: -15px;
}

.form__label {
  display: block;
  position: relative;
}

.form__input, .form__select, .form__textarea {
  position: relative;
  z-index: 1;
  display: block;
  width: 100%;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  transition: all 300ms ease;
  cursor: pointer;
  font-size: 16px;
}

@media screen and (min-width: 420px) {
  .form__input, .form__select, .form__textarea {
    font-size: 20px;
  }
}

.form__input:hover, .form__select:hover, .form__textarea:hover {
  border-color: #c0c9e2;
}

.form__input:focus, .form__select:focus, .form__textarea:focus {
  border-color: #435993;
}

.form__input:focus + .form__placeholder, .form__input.has-value + .form__placeholder, .form__select:focus + .form__placeholder, .form__select.has-value + .form__placeholder, .form__textarea:focus + .form__placeholder, .form__textarea.has-value + .form__placeholder {
  font-size: 14px;
  line-height: 30px;
  cursor: text;
}

.form__input, .form__select {
  height: 60px;
  text-align: center;
}

.form__input {
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 15px;
  background: none;
}

.form__input:focus {
  line-height: 30px;
  cursor: text;
}

.form__select {
  line-height: 60px;
  background-color: transparent;
  text-align-last: center;
}

.form__textarea {
  min-height: 180px;
  background: none;
  text-align: left;
  padding: 15px 20px;
  min-width: 100%;
  max-width: 100%;
  resize: none;
}

.form__textarea:focus + .form__placeholder, .form__textarea.has-value + .form__placeholder {
  opacity: 0;
}

.form__placeholder {
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  user-select: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 60px;
  text-align: center;
  color: #c8c8c8;
  font-size: 24px;
  font-weight: 400;
  cursor: pointer;
  transition: all 300ms ease;
}

.form__placeholder_left {
  text-align: left;
  padding-left: 20px;
}

.form__submit {
  display: block;
  width: 115px;
  height: 50px;
  line-height: 50px;
  margin-left: auto;
  margin-right: auto;
  color: #fff;
  border-radius: 7.5px;
  background: #435993;
  border: none;
  transition: background-color 300ms ease;
}

.form__submit:hover {
  background: #3b4e81;
}

.form__submit:active {
  background: #334470;
}

/* end FORM */
.checkbox {
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  user-select: none;
  line-height: 1;
  min-height: 0;
}

.checkbox__icon {
  cursor: pointer;
  font-size: 16px;
  line-height: 1;
  display: inline-block;
  vertical-align: middle;
  font-weight: 400;
}

.checkbox__icon:before {
  border: 1px solid #435993;
}

.checkbox .custom-control-label::before,
.checkbox .custom-control-label::after {
  top: 0;
}

.checkbox .custom-control-input:checked ~ .custom-control-label::after {
  background-color: #435993;
  border-radius: 3px;
}

.checkbox .custom-control-input:focus ~ .custom-control-label::before {
  box-shadow: none;
}

/*==========================================
                  HEADER
==========================================*/
.header {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  line-height: 80px;
  text-align: center;
  background: #fff;
  box-shadow: 0 2px 2px rgba(50, 50, 50, 0.1);
  transition: all 300ms ease;
}

.header.is-small {
  height: 40px;
  line-height: 40px;
}

/* end HEADER */
/*==========================================
                    MENU
==========================================*/
.menu {
  text-align: center;
  font-size: 0;
}

.menu__link {
  display: inline-block;
  vertical-align: middle;
  padding: 0 10px;
  font-size: 12px;
  line-height: 1;
  text-transform: uppercase;
  color: #494949;
  transition: color 300ms ease;
}

@media screen and (min-width: 420px) {
  .menu__link {
    padding: 0 25px;
    font-size: 14px;
  }
}

.menu__link:hover {
  color: #435993;
}

/* end MENU */
.section-background {
  height: 280px;
  margin-bottom: -80px;
  background: url("../images/background.jpg") top center no-repeat fixed;
  background-size: cover;
  box-shadow: inset 0 -150px 100px -50px #fff;
}
